.main-nav__toggle {
  display: block;
  position: relative;
  top: 31px;
  width: 25px;
  height: 3px;
  border-radius: 1.5px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
  z-index: 5;
  transition: all 0.3s ease 0s;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    top: 61px;
  }
}
@media (min-width: 1440px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 9px;
  left: 0px;
  width: 25px;
  height: 3.5px;
  border-radius: 1.5px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
  z-index: 5;
  transition: all 0.3s ease 0s;
}
.main-nav__toggle--closed {
  display: block;
  position: relative;
  top: 26px;
  right: 2px;
  width: 19px;
  height: 19px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}
.main-nav__toggle--closed::before,
.main-nav__toggle--closed::after {
  content: "";
  position: absolute;
  top: 8px;
  left: -2px;
  width: 24px;
  height: 2px;
  background-color: #444444;
  border-radius: 1.5px;
  transform: matrix(0.71, -0.73, 0.69, 0.71, 0, 0);
}
.main-nav__toggle--closed::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav__toggle--closed::after {
  transform: rotate(-45deg);
}
@media (min-width: 768px) {
  .main-nav__toggle--closed {
    top: 61px;
  }
}
@media (min-width: 1440px) {
  .main-nav__toggle--closed {
    display: none;
  }
}
